import React from 'react';
import s from './Post.module.css'

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="data:image/jpeg;base64," />
            {props.message}
            <div> 
                <span className={s.heart}>&#9825;</span>
                {props.likesCount}
            </div>
        </div>

    )
}

export default Post;